import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";


@IonicPage()
@Component({
  selector: 'page-issue-cheque-book04',
  templateUrl: 'issue-cheque-book04.html',
})
export class IssueChequeBook04Page {

  accountNo ;
  leafCount ;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public gaProvider : GoogleAnalyticsProvider) {

    this.accountNo = this.navParams.get("accountNo");
    this.leafCount = this.navParams.get("leafCount");
  }

  goToWelcome = function () {
    this.navCtrl.setRoot('WelcomePage');
    this.navCtrl.popToRoot();
  };

  issueAnotherCheque = function () {
    this.navCtrl.setRoot('WelcomePage');
    this.navCtrl.push('issueChequeBook01');
  };

  ionViewDidLoad() {
    this.gaProvider.startTrackerWithId("Cheque Book Issue Successful.");
  }
}



